import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import VueGoogleCharts from "vue-google-charts";

import {
  Container,
  Header,
  Footer,
  Main,
  Row,
  Col,
  Select,
  Option,
  Checkbox,
  Slider
} from "element-ui";
import "element-ui/lib/theme-chalk/index.css";

Vue.config.productionTip = false;

Vue.use(Container);
Vue.use(Header);
Vue.use(Footer);
Vue.use(Main);
Vue.use(Row);
Vue.use(Col);
Vue.use(Select);
Vue.use(Option);
Vue.use(Checkbox);
Vue.use(Slider);

Vue.use(VueGoogleCharts);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
