import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    selectedRace: "All Races",
    selectedCat: "All Categories",
    selectedTeam: "All Teams",
    selectedRacer: "All Racers",
    data: {
      races: [],
      cats: [],
      teams: [],
      results: []
    }
  },

  getters: {
    races: state => {
      return ["All Races"].concat(state.data.races);
    },

    cats: state => {
      return ["All Categories"].concat(state.data.cats);
    },

    teams: state => {
      return ["All Teams"].concat(state.data.teams);
    },

    racers: state => {
      return ["All Racers"].concat(state.data.racers);
    }
  },

  mutations: {
    SelectRace(state, payload) {
      state.selectedRace = payload;
    },

    SelectCat(state, payload) {
      state.selectedCat = payload;
    },

    SelectTeam(state, payload) {
      state.selectedTeam = payload;
    },

    SelectRacer(state, payload) {
      state.selectedRacer = payload;
    },

    SetData(state, payload) {
      state.data = payload;
    }
  },
  actions: {
    load(context) {
      axios
        .get("/static/data", { responseType: "json" })
        .then(response => {
          context.commit("SetData", response.data);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
});
