import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import BibRank from "./views/BibRank.vue";
import Callups from "./views/Callups.vue";

Vue.use(Router);

export default new Router({
  routes: [
    { path: "/", name: "home", component: Home },
    { path: "/bibrank", name: "bibrank", component: BibRank },
    { path: "/callups", name: "callups", component: Callups }
  ]
});
